// components/SectionSeparator.tsx
import React from 'react';
// Importa o SVG como um componente React
import SmokeSeparator from '@/public/images/nuvens/n1.svg';

const SectionSeparator = () => {
  return (
    <div className="w-full h-auto -my-12 md:-my-20 relative z-10" aria-hidden="true">
      {/* - Você pode aplicar classes do Tailwind diretamente no SVG.
        - 'h-auto' mantém a proporção.
        - 'fill-ls-preto' faria a fumaça ter a cor de fundo (se o SVG permitir).
      */}
      <SmokeSeparator className="w-full h-auto" />
    </div>
  );
};

export default SectionSeparator;